import React from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import Header from '../components/layouts/MainHeader';
import Sidebar from '../components/layouts/MainSidebar/Sidebar';
import Main from '../components/layouts/Main';
import Footer from '../components/layouts/MainFooter';
import Breadcrumb from '../components/layouts/Breadcrumb';

export default function DefaultLayout({children}) {
    return (
        <>
            <Header/>
            <div>
                <div className="b-crumb">
                    <Container>
                        <Breadcrumb/>
                    </Container>
                </div>
                <Container>
                    <Row>
                        <Col xs={3}><Sidebar/></Col>
                        <Col xs={9}><Main/>{children}</Col>
                    </Row>
                </Container>
            </div>
            <Footer/>
        </>
    )
}
