import { DefaultLayout } from "./layouts";
import Home from "./components/Home";
import Dashboard from "./components/Dashboard";


import { Introduce, Products } from "./components/navbar";

export const routes = [
    {
        path: "/",
        exact: true,
        layout: DefaultLayout,
        component: Home,
        name: 'Trang chủ'
    },
    {
        path: "/dashboard",
        layout: DefaultLayout,
        component: Dashboard,
        name: 'Admin'
    },

    /* Navbar */
    {
        path: "/gioi-thieu",
        layout: DefaultLayout,
        component: Introduce,
        name:'Giới thiệu'
    },
    {
        path: "/san-pham",
        layout: DefaultLayout,
        component: Products,
        name:'Sản phẩm'
    },
];