import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { routes } from './routes';
import {
  BrowserRouter,
  Route,
} from 'react-router-dom';
function App() {
  return (
    <BrowserRouter>
      <div>
        {routes.map((route, index) => {
          return(
            <Route key={index} path={route.path} exact={route.exact} name={route.name} component={(props => {
              return(
                <route.layout {...props}>
                  <route.component {...props}/>
                </route.layout>
              );
            })}>
            </Route>
          );
        })}
      </div>
    </BrowserRouter>
  );
}

export default App;
