import { 
    Row,
    Col,
    Image, InputGroup, FormControl, Button, Container
} 
from 'react-bootstrap';
import React from 'react';
import reactpng from "./../../../assets/reactjs.png";

export default function HeaderCenter() {
    return (
        <div>
        <Container>
            <Row>
                <Col md={3} style={{display: 'flex', justifyContent:'center'}}>
                    <Image src={reactpng} className="img-fluid" alt="" style={{height:100}}/>
                </Col>
                <Col md={6} style={{alignSelf:'center'}}>
                    <InputGroup className="mb-3">
                        <FormControl
                            placeholder="Tìm kiếm ..."
                            aria-label="Recipient's username"
                            aria-describedby="basic-addon2"
                        />
                        <InputGroup.Append>
                            <Button variant="outline-secondary">Button</Button>
                        </InputGroup.Append>
                    </InputGroup>
                </Col>
                <Col md={3} style={{display: 'flex', justifyContent:'center', alignSelf:'center'}}>
                    <p>Hỗ trợ khách hàng</p>
                </Col>
            </Row>
        </Container>
        </div>
    )
}